.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
  /* products */
  .container {
    max-width: 375px;
    padding: 0 16px;
    margin: 0 auto;
  }
  .products {
    background-color: #ffffff;
    padding-top: 100px;
  }
  
  .products-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 16px;
  }
  
  .products-item {
    background-color: #ffffff;
    display: inline-block;
  }
  .products-item:last-child {
    margin-bottom: 0;
  }
  
  .products-header {
    font-size: 32px;
    font-weight: 400;
    line-height: 45px;
    text-align: center;
    color: #0a224a;
    margin-bottom: 33px;
  }
  
  .products-desc {
    font-size: 18px;
    font-weight: 400;
    line-height: 27px;
    margin-top: 16px;
    color: #0a224a;
  }

  @media screen and (min-width: 1440px) {
    .container {
      max-width: 1440px;
      padding: 0 15px;
    }
  .products-list {
    flex-wrap: nowrap;
    gap: 16px;
  }
  .products-photo {
    width: 312px;
  }
}
@media screen and (min-width: 768px) and (max-width: 1440px) {
    .container {
      max-width: 768px;
      padding: 0 16px;
    } 
}
